class EventBus extends EventTarget {
    fire(name, detail = {}) {
        this.dispatchEvent(new CustomEvent(name, { detail }));
    }

    dispatchEvent(e) {
        console.log(`Dispatched ${e.type}:`, e.detail);
        super.dispatchEvent(e);
    }
}

const eventBus = new EventBus();
export { EventBus, eventBus };
